button.slick-prev, button.slick-next{
  background:#fff;
  width: 56px;
  height: 56px;
  transform: translate(0px,-50%);
  z-index: 1;
  border-radius: 50%;
  margin: 0;
  
  }

  button.slick-next:before,button.slick-prev:before{
    content: '' !important;
    position: absolute;
    top: 50%;
    opacity: 1;
    width: 12px;
    left: 4px;
    right: 0;
    margin: -6px auto 0px auto;
    height: 12px;
    border-top: 1px solid #000;
    border-left: 1px solid #000;
    transform: rotate(
135deg
);
  }
  button.slick-prev:before{
    transform: rotate(
-45deg
);
left: -4px;
  }
  button.slick-next::after{
    content: '';
    position: absolute;
    width: 20px;
    height: 1px;
    background: #000;
    left: 0;
    right: 0;
    margin: 0 auto;
  }
  button.slick-prev::after{
    content: '';
    position: absolute;
    width: 20px;
    height: 1px;
    background: #000;
    left: 0;
    right: 0;
    margin: 0 auto;
  }

